* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 70%;
  height: auto;
  margin: 0 auto;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  padding: 20px;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 20px;
  margin: 10px 0;
  padding: 12px 0;
}

.greeting_container {
  padding: 2px 20px;
}
.greeting_container .greeting_text {
  color: inherit;
  font-family: inherit;
}

.darkmode {
  background-color: #fff;
  padding: 5px;
  font-family: inherit;
  cursor: pointer;
  outline: none;
}

.toggleDarkmode {
  background-color: black;
  color: #fff !important;
}

.dropdown_key_box .youtube-link {
  color: inherit;
}
.dropdown_key_box .dropdown_data_ui {
  border: 1px solid silver;
  width: 150px;
  height: auto;
  margin: 15px 0 7px 0;
  padding: 5px;
  display: none;
}
.dropdown_key_box .renderUi {
  display: block;
}

